


































































@import '@design';

.quoteContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 90vw;
  height: 70vh;

  h3 {
    width: 80vw;
    line-height: 1.6;
    text-align: center;
  }

  .startQuote,
  .endQuote {
    width: 30px;
    height: 30px;
    margin-right: 45px;
    font-size: 180px;
    font-style: italic;
    color: lightgray;
  }

  .startQuote {
    position: fixed;
    bottom: 30px;
    left: 0;
  }

  .endQuote {
    position: fixed;
    top: 30vh;
    right: 30px;
  }
}
